.movie-list {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 20px;
  padding: 20px;
}

/* 태블릿 크기 이상일 때 (768px 이상) */
@media screen and (min-width: 768px) {
  .movie-list {
    grid-template-columns: repeat(2, minmax(400px, 1fr));
  }
}

/* 작은 모바일 화면에서는 여백 줄이기 */
@media screen and (max-width: 480px) {
  .movie-list {
    padding: 8px;
    gap: 8px;
  }

  .movie-item {
    flex-direction: column;
  }

  .movie-image {
    width: 100%;
    height: 200px;
  }
}

.movie-item {
  display: flex;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.2s;
  height: 40vh;
}

.movie-item:hover {
  transform: scale(1.05);
}

.movie-image {
  width: 150px;
  height: auto;
  object-fit: cover;
}

.movie-details {
  padding: 16px;
  flex: 1;
  overflow-y: auto;
  max-height: 100%;
}

/* Link 스타일 추가 */
.movie-list a {
  text-decoration: none;
  color: inherit;
}

/* 모바일 화면에서는 다른 높이 적용 */
@media screen and (max-width: 480px) {
  .movie-item {
    height: auto;
    max-height: 70vh;
  }
}

.movie-genres {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  padding: 0;
  list-style: none;
}

.movie-genres li {
  background-color: #e9ecef;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.9em;
}

/* 태블릿 크기일 때 */
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .movie-item {
    height: 45vh;
  }
}
